import axios from 'axios';
import {PostsAPILink} from './data';
require('../scss/pages/post.scss');

const NotFound = (placeholder) => {
  placeholder.innerHTML = '<p>Article Not Found</p>';
}

window.addEventListener('load', ()=>{
  //if no url parameter redirect to home
  const placeholder = document.getElementById('post-wrapper');
  const queryString = window.location.search;
  const urlParams = new URLSearchParams(queryString);
  const articleId = urlParams.get('articleId');
  if(!articleId) {
    NotFound(placeholder);
  }

  axios.get(`${PostsAPILink}posts/${articleId}`).then((res)=>{
    placeholder.innerHTML = `
      <h1>${res.data.title.rendered}</h1>
      ${res.data.content.rendered}
    `
  })
  .catch((err)=>{
    console.log(`Error retrieving posts - ${err}`)
  });

});